import React, { useState } from "react";
import css from "classnames";
import axios from "axios";
import Popover from "./Popover";

import ScriptureRef from "./ScriptureRef";

const ConfessionParagraph = ({ paragraph, index }) => {
  const [activeScripture, setActiveScripture] = useState(null);
  const [activeScriptureText, setActiveScriptureText] = useState(null);
  const [loading, setLoading] = useState(false);

  return (
    <div>
      <h4>Chapter {index + 1}</h4>
      <p>
        {paragraph.map(section => {
          return (
            <span key={section.superscript}>
              {section.text}
              <a
                href={`#ref-${index}-${section.superscript}`}
                id={`${index}-${section.superscript}`}
                className="superscript"
              >{`${section.superscript} `}</a>
            </span>
          );
        })}
      </p>

      <div className="scripture-section">
        <p className="nomargin">
          <strong>References (click to view)</strong>
        </p>
        {paragraph.map(section => {
          return (
            <p key={section.superscript} className="nomargin">
              <a
                href={`#${index}-${section.superscript}`}
                id={`ref-${index}-${section.superscript}`}
              >
                {`^ `}
              </a>
              <strong>{section.superscript}.</strong>{" "}
              {section.scriptures
                ? section.scriptures.map((scripture, index) => {
                    const classNames = css("scripture", {
                      "scripture--active": scripture === activeScripture
                    });

                    return (
                      <span key={scripture}>
                        <Popover
                          isOpen={scripture === activeScripture}
                          body={
                            activeScriptureText ? (
                              loading ? (
                                <img
                                  src="https://i.imgur.com/P7fXP4s.gif"
                                  style={{
                                    display: "flex",
                                    height: 25,
                                    width: 25,
                                    textAlign: "center",
                                    margin: "8px auto 0"
                                  }}
                                />
                              ) : (
                                <ScriptureRef
                                  key={index}
                                  data={activeScriptureText}
                                />
                              )
                            ) : null
                          }
                          place="below"
                        >
                          <span
                            className={classNames}
                            onClick={() => {
                              const savedActiveScripture =
                                scripture === activeScripture
                                  ? null
                                  : scripture;

                              setActiveScripture(savedActiveScripture);

                              if (savedActiveScripture) {
                                setLoading(true);

                                axios
                                  .get(
                                    `https://bibleanswersapi.herokuapp.com/passages?passages=${encodeURIComponent(
                                      savedActiveScripture
                                    )}`
                                  )
                                  .then(response => {
                                    setLoading(false);
                                    setActiveScriptureText(response.data);
                                  });
                              } else {
                                setActiveScriptureText(null);
                              }
                            }}
                          >
                            {scripture}
                          </span>
                        </Popover>
                        {index === section.scriptures.length - 1 ? "" : `; `}
                      </span>
                    );
                  })
                : null}
            </p>
          );
        })}

        {/* {activeScriptureText ? (
          loading ? (
            <img
              src="https://i.imgur.com/P7fXP4s.gif"
              style={{
                display: "flex",
                height: 25,
                width: 25,
                textAlign: "center",
                margin: "8px auto 0"
              }}
            />
          ) : (
            <ScriptureRef key={index} data={activeScriptureText} />
          )
        ) : null} */}
      </div>

      <style jsx>{`
        h4 {
          font-size: 26px;
          line-height: 1.22;
          margin: 0;
          margin-top: 30px;
        }

        p {
          font-size: 21px;
          line-height: 1.58;
          margin: 0;
          margin-top: 29px;
        }

        h4 + p {
          margin-top: 8px;
        }

        .first {
          margin-top: 8px;
        }

        .nomargin {
          margin-top: 0;
        }

        a {
          color: #689f38;
          text-decoration: none;
        }

        @media screen and (max-width: 768px) {
          h4 {
            font-size: 24px;
            margin-top: 22px;
          }

          p {
            font-size: 18px;
            margin-top: 21px;
          }
        }

        .superscript {
          color: #689f38;
          font-size: 15px;
          margin-left: 1px;
          position: relative;
          top: -6px;
        }

        .scripture-section {
          border: 1px solid #bdbdbd;
          margin-top: 20px;
          padding: 5px 10px;
        }

        .scripture {
          color: rgba(0, 0, 0, 0.54);
          cursor: pointer;
        }

        .scripture--active {
          color: rgba(0, 0, 0, 0.84);
          font-weight: bold;
        }
      `}</style>
    </div>
  );
};

export default ConfessionParagraph;
